<template>
  <div class="fixed inset-0 flex items-center justify-center z-[10001] backdrop-blur-sm">
    <div class="bg-app-bg bg-opacity-95 border-2 border-app-accent rounded-xl p-5 w-[min(92vw,420px)] text-center shadow-2xl">
      <h3 class="text-lg text-app-accent font-medium mb-2">
        🎬 錄製月相影片中
      </h3>
      <div class="text-sm text-app-muted mb-3">
        靜止人物 + 動態月相背景
      </div>
      
      <!-- Progress Bar -->
      <div class="mb-3">
        <div class="bg-white bg-opacity-10 h-3 rounded-full overflow-hidden">
          <div 
            class="h-full bg-gradient-to-r from-app-accent to-yellow-600 transition-all duration-300"
            :style="{ width: progressPercent + '%' }"
          />
        </div>
        <div class="text-app-accent font-bold mt-2">{{ status }}</div>
        <div class="text-app-muted text-sm mt-1">{{ progressTime }}</div>
      </div>
      
      <div class="text-app-muted text-sm mb-4">
        {{ showControls ? '影片已生成：可直接分享或下載（建議先分享）' : '影片生成後會顯示分享與下載按鈕' }}
      </div>

      <!-- Controls -->
      <div class="flex gap-2 justify-center">
        <button 
          v-if="showControls"
          @click="$emit('download')"
          class="px-3 py-2 rounded-lg border border-app-border bg-app-bg text-app-fg hover:bg-opacity-80 transition-all"
        >
          ⬇ 下載
        </button>
        <button 
          v-if="showControls"
          @click="$emit('share')"
          class="px-3 py-2 rounded-lg border border-app-border bg-app-accent text-app-bg hover:bg-opacity-80 transition-all"
        >
          🔗 分享
        </button>
        <button 
          @click="$emit('close')"
          class="px-3 py-2 rounded-lg border border-app-border bg-transparent text-app-muted hover:bg-opacity-80 transition-all"
        >
          關閉
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'ProgressModal',
  props: {
    progress: {
      type: Object,
      default: () => ({ percent: 0, current: 0, total: 8 })
    },
    status: {
      type: String,
      default: '準備錄製...'
    },
    showControls: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close', 'share', 'download'],
  setup(props) {
    const progressPercent = computed(() => {
      return Math.round(props.progress?.percent || 0)
    })

    const progressTime = computed(() => {
      const current = props.progress?.current || 0
      const total = props.progress?.total || 8
      return `${current.toFixed(1)}s / ${total.toFixed(1)}s`
    })

    return {
      progressPercent,
      progressTime
    }
  }
}
</script>