import { ref, watch } from 'vue'

export function useMediaPipe(uiRefs) {
  // MediaPipe state
  const cameraRunning = ref(false)
  const segmentationEnabled = ref(false)
  const showMask = ref(false)
  const backgroundType = ref('custom_moon_bg')
  const effectIntensity = ref(0.5)
  const edgeFeather = ref(3)
  const temporalSmooth = ref(0.6)
  const threshold = ref(0.38)
  
  // 儲存影片元素引用
  let bgMoonVideoElement = null
  let iosMoonVideoElement = null
  let fullscreenMoonElement = null
  let isIOSDevice = false
  
  // Statistics
  const faceCount = ref(0)
  const facesInArea = ref(0)
  
  // Core loaded state
  const coreLoaded = ref(false)
  
  // 初始化 MediaPipe 核心
  const initMediaPipe = async (elements) => {
    try {
      // 動態導入核心模組
      const coreModule = await import('../segmentationCore.js')
      window.segmentationCore = coreModule
      
      // 設定回調函數
      const callbacks = {
        onStatus: (status) => {
          console.log('📡 收到狀態:', status)
          
          // 處理特殊觸發狀態
          if (status === 'TRIGGER_TEST_EFFECT') {
            console.log('🎯 觸發測試效果 - 呼叫 testMovBackground')
            testMovBackground();
            return;
          }
          
          if (status === 'STOP_TEST_EFFECT') {
            // 恢復正常相機模式（原始畫面，無月相效果）
            segmentationEnabled.value = false;
            backgroundType.value = 'original';
            if (coreLoaded.value) {
              window.segmentationCore.setEnableSegmentation(false);
              window.segmentationCore.setBackgroundType('original');
            }
            // 暫停月相影片
            if (isIOSDevice && fullscreenMoonElement && !fullscreenMoonElement.paused) {
              fullscreenMoonElement.pause();
            } else if (bgMoonVideoElement && !bgMoonVideoElement.paused) {
              bgMoonVideoElement.pause();
            }
            uiRefs.detectionStatus.value = '檢測中';
            return;
          }
          
          // 正常狀態更新
          uiRefs.statusText.value = status
          uiRefs.detectionStatus.value = status
        },
        onStats: (stats) => {
          faceCount.value = stats.faceCount || 0
          facesInArea.value = stats.facesInArea || 0
          
          // 同步人臉偵測觸發的狀態變化
          const prevSegmentation = segmentationEnabled.value
          const prevBackground = backgroundType.value
          
          if (stats.segmentationEnabled !== undefined) {
            segmentationEnabled.value = stats.segmentationEnabled
          }
          if (stats.backgroundType !== undefined) {
            backgroundType.value = stats.backgroundType
          }
          
          // 只在狀態變化時更新提示和記錄日誌
          if (stats.effectActive !== undefined) {
            if (stats.effectActive && stats.backgroundType === 'moon_video') {
              uiRefs.detectionStatus.value = '🌙 月相模式：已啟用'
            } else if (!stats.effectActive && stats.backgroundType === 'original') {
              uiRefs.detectionStatus.value = '檢測中'
            }
          }
          
          // 只在狀態真正改變時記錄
          if (segmentationEnabled.value !== prevSegmentation || backgroundType.value !== prevBackground) {
            console.log('📊 UI 狀態變化:', {
              faces: `${stats.faceCount}/${stats.facesInArea}`,
              segmentation: `${prevSegmentation}→${segmentationEnabled.value}`,
              background: `${prevBackground}→${backgroundType.value}`,
              effectActive: stats.effectActive
            })
          }
        }
      }
      
      // 儲存影片元素引用
      bgMoonVideoElement = elements.bgMoonVideoEl
      iosMoonVideoElement = elements.iosMoonVideoEl
      fullscreenMoonElement = elements.fullscreenMoonEl
      isIOSDevice = elements.isIOSDevice || false
      
      console.log('🔧 MediaPipe 初始化:', {
        hasNormalMoonVideo: !!bgMoonVideoElement,
        hasIOSMoonVideo: !!iosMoonVideoElement,
        hasFullscreenMoonVideo: !!fullscreenMoonElement,
        fullscreenMoonElementType: fullscreenMoonElement?.constructor?.name,
        fullscreenMoonElementSrc: fullscreenMoonElement?.src,
        isIOSDevice
      })
      
      
      // 初始化核心
      await coreModule.initCore(elements, callbacks)
      coreLoaded.value = true
      
      // 確保初始設定被正確應用
      coreModule.setBackgroundType(backgroundType.value)
      // 明確禁用分割以確保預覽模式
      segmentationEnabled.value = false
      coreModule.setEnableSegmentation(false)
      console.log('🚫 分割功能已禁用，進入預覽模式')
      
      // 設定錄影進度回調
      if (coreModule.setRecordingProgressCallback) {
        coreModule.setRecordingProgressCallback({
          updateProgress: (current, total) => {
            uiRefs.recordingProgress.value = {
              percent: total > 0 ? (current / total) * 100 : 0,
              current,
              total
            }
          },
          setStatus: (status) => {
            uiRefs.progressStatus.value = status
          }
        })
      }
      
      console.log('MediaPipe core initialized')
      
      // 預載月相影片但保持暫停，等待人像偵測觸發
      if (bgMoonVideoElement) {
        bgMoonVideoElement.load()
      }
      
      // iOS 設備預載透明 MOV
      if (iosMoonVideoElement && isIOSDevice) {
        iosMoonVideoElement.load()
        console.log('🍎 iOS MOV 影片預載完成')
      }
      
    } catch (error) {
      console.error('Failed to load MediaPipe core:', error)
      uiRefs.detectionStatus.value = '核心載入失敗'
    }
  }

  // Camera controls
  const startCamera = async () => {
    if (!coreLoaded.value) {
      return
    }
    
    uiRefs.detectionStatus.value = '啟動中...'
    try {
      await window.segmentationCore.start()
      cameraRunning.value = true
      uiRefs.detectionStatus.value = '檢測中'
    } catch (error) {
      console.error('Camera start failed:', error)
      uiRefs.detectionStatus.value = '啟動失敗'
      
      // 錯誤處理
      let errorMessage = "啟動失敗"
      if (error.name === "NotAllowedError" || (error.message && error.message.includes("Permission denied"))) {
        errorMessage = "相機權限被拒絕，請在瀏覽器設定中允許相機使用權限，然後重新整理頁面"
      } else if (error.name === "NotFoundError") {
        errorMessage = "找不到相機設備"
      } else if (error.name === "NotSupportedError") {
        errorMessage = "瀏覽器不支援相機功能"
      } else if (error.message && error.message.includes("MediaPipe")) {
        errorMessage = "AI 模型載入失敗，請檢查網路連線"
      }
      
      alert(errorMessage)
    }
  }

  const stopCamera = () => {
    if (!coreLoaded.value) return
    
    window.segmentationCore.stop()
    cameraRunning.value = false
    uiRefs.detectionStatus.value = '已停止'
    uiRefs.statusText.value = 'Idle'
  }

  const switchCamera = async () => {
    if (!coreLoaded.value || !cameraRunning.value) return
    
    try {
      await window.segmentationCore.switchCamera()
    } catch (error) {
      console.warn('Switch camera failed:', error)
    }
  }

  // Settings updates
  const updateSegmentation = (enabled) => {
    segmentationEnabled.value = enabled
    if (coreLoaded.value) {
      window.segmentationCore.setEnableSegmentation(enabled)
    }
    if (!enabled) {
      showMask.value = false
      updateShowMask(false)
    }
  }

  const updateShowMask = (show) => {
    showMask.value = show
    if (coreLoaded.value) {
      window.segmentationCore.setShowMaskPreview(show)
    }
  }

  const updateBackgroundType = (type) => {
    backgroundType.value = type
    if (coreLoaded.value) {
      window.segmentationCore.setBackgroundType(type)
    }
    
    // 處理月相影片載入
    if ((type === 'moon_video' || type === 'custom_moon_bg') && (bgMoonVideoElement || iosMoonVideoElement)) {
      uiRefs.detectionStatus.value = "載入月相影片..."
      
      // iOS 設備優先使用透明 MOV
      if (isIOSDevice && iosMoonVideoElement) {
        if (iosMoonVideoElement.paused) {
          iosMoonVideoElement.play()
            .then(() => {
              uiRefs.detectionStatus.value = "🍎 iOS 透明月相影片已開始播放"
              setTimeout(() => {
                uiRefs.detectionStatus.value = "檢測中"
              }, 2000)
            })
            .catch((e) => {
              console.warn("iOS 月相影片播放失敗:", e)
              uiRefs.detectionStatus.value = "iOS 月相影片播放失敗"
            })
        }
      } else if (bgMoonVideoElement) {
        // 非 iOS 設備使用傳統 MP4
        if (bgMoonVideoElement.paused) {
          bgMoonVideoElement.play()
            .then(() => {
              uiRefs.detectionStatus.value = "月相影片已開始播放"
              setTimeout(() => {
                uiRefs.detectionStatus.value = "檢測中"
              }, 2000)
            })
            .catch((e) => {
              console.warn("月相影片播放失敗:", e)
              uiRefs.detectionStatus.value = "月相影片播放失敗"
            })
        }
      }
    }
  }

  const updateEffectIntensity = (intensity) => {
    effectIntensity.value = intensity
    if (coreLoaded.value) {
      window.segmentationCore.setBackgroundIntensity(intensity)
    }
  }

  const updateEdgeFeather = (feather) => {
    edgeFeather.value = feather
    if (coreLoaded.value) {
      window.segmentationCore.setEdgeFeather(feather)
    }
  }

  const updateTemporalSmooth = (smooth) => {
    temporalSmooth.value = smooth
    if (coreLoaded.value) {
      window.segmentationCore.setTemporalAlpha(smooth)
    }
  }

  const updateThreshold = (thresholdValue) => {
    threshold.value = thresholdValue
    if (coreLoaded.value) {
      window.segmentationCore.setThreshold(thresholdValue)
    }
  }

  // Photo & Video  
  const capturePhoto = async () => {
    if (!coreLoaded.value || !cameraRunning.value) {
      alert('請先啟動相機再拍照')
      return
    }
    
    // 拍照效果
    const flash = document.createElement('div')
    flash.className = 'fixed inset-0 bg-white opacity-80 z-[9999] pointer-events-none animate-photo-flash'
    document.body.appendChild(flash)
    setTimeout(() => document.body.removeChild(flash), 200)
    
    try {
      // 先臨時啟用分割來獲取最新的人像蒙版數據
      window.segmentationCore.setEnableSegmentation(true)
      
      // 等待幾幀讓分割處理當前畫面
      await new Promise(resolve => setTimeout(resolve, 200))
      
      // 直接捕獲原始相機畫面和合成畫面，不使用 recordingManager
      const originalImageData = await window.segmentationCore.captureOriginalFrame()
      
      // 手動獲取分割後的合成畫面
      let segmentedImageData = null
      try {
        // 獲取 outCanvas（已經有分割後的合成畫面）
        const canvasElements = document.querySelectorAll('.output_canvas')
        if (canvasElements.length > 0) {
          const outCanvas = canvasElements[0]
          segmentedImageData = outCanvas.toDataURL('image/png')
        }
      } catch (error) {
        console.warn('無法獲取分割後畫面，使用原始畫面:', error)
        segmentedImageData = originalImageData
      }
      
      console.log('🔍 拍照數據檢查:', {
        hasOriginal: !!originalImageData,
        hasSegmented: !!segmentedImageData,
        originalLength: originalImageData?.length,
        segmentedLength: segmentedImageData?.length
      })
      
      // 立即關閉分割，回到預覽模式
      window.segmentationCore.setEnableSegmentation(false)
      
      if (originalImageData) {
        // 進入預覽模式 - 停止相機和人臉檢測
        console.log('📸 進入預覽模式，停止相機和人臉檢測')
        
        // 停止所有檢測和渲染循環
        cameraRunning.value = false
        if (window.segmentationCore?.stop) {
          window.segmentationCore.stop()
        }
        
        // 暫停月相影片（拍照後由 PhotoPreview 組件控制）
        if (isIOSDevice && fullscreenMoonElement && !fullscreenMoonElement.paused) {
          fullscreenMoonElement.pause()
        } else if (bgMoonVideoElement && !bgMoonVideoElement.paused) {
          bgMoonVideoElement.pause()
        }
        
        // 顯示預覽模式（PhotoPreview 組件會自己處理渲染）
        console.log('🔍 準備調用 showPreview:', {
          hasShowPreviewFunction: !!uiRefs.showPreview,
          uiRefsKeys: Object.keys(uiRefs)
        })
        
        if (uiRefs.showPreview) {
          console.log('📱 調用 showPreview 函數')
          uiRefs.showPreview({
            originalImageData,
            segmentedImageData
          })
        } else {
          console.error('❌ showPreview 函數不存在！')
        }
        uiRefs.detectionStatus.value = '📸 拍照成功！選擇背景'
        
        setTimeout(() => {
          uiRefs.detectionStatus.value = '預覽模式'
        }, 2000)
      } else {
        alert('拍照失敗，請重試')
      }
    } catch (error) {
      console.error('Capture photo failed:', error)
      // 確保分割被關閉
      window.segmentationCore.setEnableSegmentation(false)
      alert('拍照失敗，請重試')
    }
  }


  const toggleMask = () => {
    updateShowMask(!showMask.value)
  }

  const shareVideo = async () => {
    if (!coreLoaded.value || !window.segmentationCore.shareLastRecording) return
    
    try {
      const result = await window.segmentationCore.shareLastRecording()
      console.log('分享結果：', result)
      if (result && result.url) {
        alert('分享成功或已上傳: ' + result.url)
      } else {
        alert('分享已觸發（系統面板或已完成上傳）')
      }
    } catch (error) {
      console.error('分享失敗：', error)
      alert('分享失敗，建議先下載再手動分享')
    }
  }

  const downloadVideo = () => {
    if (!coreLoaded.value || !window.segmentationCore.getLastRecordingUrl) return
    
    const url = window.segmentationCore.getLastRecordingUrl()
    if (!url) {
      alert('目前沒有可下載的影片')
      return
    }
    
    const a = document.createElement('a')
    a.href = url
    a.download = ''
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    
    setTimeout(() => {
      try {
        URL.revokeObjectURL(url)
      } catch (e) {}
    }, 1500)
  }

  const handleResize = () => {
    if (window.segmentationCore?.onResize) {
      window.segmentationCore.onResize()
    }
  }

  const testMovBackground = () => {
    console.log('🌙 testMovBackground 被觸發')
    
    if (!coreLoaded.value) {
      console.warn('⚠️ 核心未載入，無法播放 MOV')
      alert('請先啟動相機再測試')
      return
    }
    
    // 人像覆蓋模式：啟用分割用於人像提取，背景顯示原始相機畫面
    backgroundType.value = 'person_overlay'  // 使用人像覆蓋模式
    segmentationEnabled.value = true   // 啟用分割來獲取人像蒙版
    
    console.log('🔧 檢查核心狀態:', {
      coreLoaded: coreLoaded.value,
      hasSegmentationCore: !!window.segmentationCore,
      hasSetBackgroundType: !!window.segmentationCore?.setBackgroundType
    })
    
    if (coreLoaded.value) {
      // 啟用人像分割，但用於人像提取而非背景替換
      window.segmentationCore.setEnableSegmentation(true)
      window.segmentationCore.setBackgroundType('person_overlay')
      console.log('✅ 人像覆蓋模式已啟用，人像將顯示在月亮影片之上')
    } else {
      console.warn('⚠️ 核心未載入，無法設定人像覆蓋模式')
    }
    
    // 播放月相影片
    console.log('📹 檢查 MOV 播放條件:', {
      isIOSDevice,
      hasIOSMoonVideo: !!iosMoonVideoElement,
      hasFullscreenMoonVideo: !!fullscreenMoonElement,
      hasBgMoonVideo: !!bgMoonVideoElement,
      iosPaused: iosMoonVideoElement?.paused,
      fullscreenPaused: fullscreenMoonElement?.paused,
      bgPaused: bgMoonVideoElement?.paused
    })
    
    if (isIOSDevice && fullscreenMoonElement) {
      // iOS 設備使用全螢幕透明 MOV
      console.log('🍎 嘗試播放 iOS 全螢幕月相影片')
      
      // 重要！拍照前只播放月相影片，不啟用人像分割
      // segmentationEnabled 保持 false，只設定背景類型觸發影片播放
      
      if (fullscreenMoonElement.paused) {
        fullscreenMoonElement.play()
          .then(() => {
            console.log('✅ iOS 全螢幕月相影片播放成功')
            uiRefs.detectionStatus.value = '🍎🌙 iOS 透明月相模式：已啟用'
          })
          .catch((e) => {
            console.warn('❌ iOS 全螢幕月相影片播放失敗:', e)
            uiRefs.detectionStatus.value = 'iOS 影片播放失敗'
          })
      } else {
        console.log('ℹ️ iOS 全螢幕月相影片已在播放中')
        uiRefs.detectionStatus.value = '🍎🌙 iOS 透明月相模式：已啟用'
      }
    } else if (bgMoonVideoElement) {
      // 非 iOS 設備使用 MP4
      console.log('🌙 嘗試播放月相影片 (非 iOS)')
      if (bgMoonVideoElement.paused) {
        bgMoonVideoElement.play()
          .then(() => {
            console.log('✅ 月相影片播放成功')
            uiRefs.detectionStatus.value = '🌙 自定義背景模式：已啟用'
          })
          .catch((e) => {
            console.warn('❌ 月相影片播放失敗:', e)
            uiRefs.detectionStatus.value = '影片播放失敗'
          })
      } else {
        console.log('ℹ️ 月相影片已在播放中')
      }
    } else {
      console.warn('⚠️ 找不到可播放的月相影片元素')
    }
  }

  return {
    // State
    cameraRunning,
    segmentationEnabled,
    showMask,
    backgroundType,
    effectIntensity,
    edgeFeather,
    temporalSmooth,
    threshold,
    faceCount,
    facesInArea,
    coreLoaded,
    
    // Methods
    initMediaPipe,
    startCamera,
    stopCamera,
    switchCamera,
    updateSegmentation,
    updateShowMask,
    updateBackgroundType,
    updateEffectIntensity,
    updateEdgeFeather,
    updateTemporalSmooth,
    updateThreshold,
    capturePhoto,
    toggleMask,
    shareVideo,
    downloadVideo,
    handleResize,
    testMovBackground
  }
}