import { ref, nextTick } from 'vue'

export function useUI() {
  // UI state
  const mobileMenuOpen = ref(false)
  const detectionStatus = ref('尚未啟動')
  const statusText = ref('Idle')
  
  // Modal state
  const showLoadingModal = ref(false)
  const loadingMessage = ref('')
  const showProgressModal = ref(false)
  const recordingProgress = ref({ percent: 0, current: 0, total: 8 })
  const progressStatus = ref('準備錄製...')
  const showProgressControls = ref(false)
  
  // Photo preview state
  const showPhotoPreview = ref(false)
  const previewImageData = ref(null)
  const availableBackgrounds = ref([
    { 
      id: 'bg1', 
      name: '月夜景觀', 
      src: 'images/pexels-photo-623218.jpeg',
      frame: 'images/bgDefender.png',
      contentArea: { x: 0.05, y: 0.25, width: 0.9, height: 0.5 }, // 調整為符合框架透明區域
      moonScale: 1.5 // 月亮影片放大倍數
    },
    { 
      id: 'bg2', 
      name: '景色', 
      src: 'images/view.jpg',
      frame: 'images/bgDiscovery.png',
      contentArea: { x: 0.05, y: 0.25, width: 0.9, height: 0.5 }, // Discovery 框架的透明區域
      moonScale: 1.5 // 月亮影片放大倍數
    },
    { 
      id: 'bg3', 
      name: '城市', 
      src: 'images/images.jpeg',
      frame: 'images/bgRangerover.png',
      contentArea: { x: 0.05, y: 0.25, width: 0.9, height: 0.5 }, // Range Rover 框架的透明區域
      moonScale: 1.5 // 月亮影片放大倍數
    }
  ])
  const selectedBackground = ref('bg1')
  const previewWithSegmentation = ref(false)

  // Methods
  const toggleMobileMenu = () => {
    mobileMenuOpen.value = !mobileMenuOpen.value
    if (mobileMenuOpen.value) {
      document.body.classList.add('panel-open')
    } else {
      document.body.classList.remove('panel-open')
    }
    // 延遲呼叫 onResize 來確保畫布正確調整
    setTimeout(() => {
      if (window.segmentationCore?.onResize) {
        window.segmentationCore.onResize()
      }
    }, 260)
  }

  const closeProgressModal = () => {
    showProgressModal.value = false
  }

  const shareFromModal = async () => {
    if (window.segmentationCore?.shareLastRecording) {
      try {
        await window.segmentationCore.shareLastRecording()
      } catch (error) {
        console.error('分享失敗：', error)
        alert('分享失敗，建議先下載再手動分享')
      }
    }
  }

  const downloadFromModal = () => {
    if (window.segmentationCore?.getLastRecordingUrl) {
      const url = window.segmentationCore.getLastRecordingUrl()
      if (url) {
        const a = document.createElement('a')
        a.href = url
        a.download = ''
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        
        setTimeout(() => {
          try {
            URL.revokeObjectURL(url)
          } catch (e) {}
        }, 1500)
      }
    }
  }

  const createPhotoFlashEffect = () => {
    const flash = document.createElement('div')
    flash.className = 'fixed inset-0 bg-white opacity-80 z-[9999] pointer-events-none animate-photo-flash'
    document.body.appendChild(flash)
    setTimeout(() => document.body.removeChild(flash), 200)
  }

  // Photo preview methods
  const showPreview = (imageData) => {
    previewImageData.value = imageData
    showPhotoPreview.value = true
  }

  const hidePreview = () => {
    showPhotoPreview.value = false
    previewImageData.value = null
  }

  const selectBackground = (backgroundId) => {
    selectedBackground.value = backgroundId
    // 觸發重新合成預覽圖片
    if (previewImageData.value && window.segmentationCore?.recomposePreview) {
      window.segmentationCore.recomposePreview(backgroundId)
    }
  }

  const initKeyboardShortcuts = ({ startCamera, stopCamera, switchCamera, capturePhoto, cameraRunning }) => {
    document.addEventListener('keydown', (e) => {
      if (e.key === ' ') {
        e.preventDefault()
        cameraRunning.value ? stopCamera() : startCamera()
      }
      if (e.key.toLowerCase() === 'c' && cameraRunning.value) {
        switchCamera()
      }
      if (e.key.toLowerCase() === 'p' && cameraRunning.value) {
        e.preventDefault()
        capturePhoto()
      }
    })
  }

  return {
    // State
    mobileMenuOpen,
    detectionStatus,
    statusText,
    showLoadingModal,
    loadingMessage,
    showProgressModal,
    recordingProgress,
    progressStatus,
    showProgressControls,
    
    // Photo preview state
    showPhotoPreview,
    previewImageData,
    availableBackgrounds,
    selectedBackground,
    previewWithSegmentation,
    
    // Methods
    toggleMobileMenu,
    closeProgressModal,
    shareFromModal,
    downloadFromModal,
    createPhotoFlashEffect,
    showPreview,
    hidePreview,
    selectBackground,
    initKeyboardShortcuts
  }
}