<template>
  <div class="landing-page min-h-screen bg-gradient-to-b from-slate-900 via-slate-800 to-amber-900 relative overflow-hidden">
    <!-- Background Image/Video placeholder -->
    <div class="absolute inset-0 bg-black/20"></div>
    
    <!-- Background car image placeholder -->
    <div class="absolute inset-0 flex items-center justify-center opacity-30">
      <div class="w-full h-full bg-gradient-to-t from-black/60 to-transparent"></div>
    </div>
    
    <!-- Main Content -->
    <div class="relative z-10 min-h-screen flex flex-col justify-center items-center px-6 py-12 text-center text-white">
      <!-- Title -->
      <div class="mb-8">
        <h1 class="text-4xl md:text-5xl font-bold mb-4 leading-tight">
          珍藏賞月中的<br />珍貴影像
        </h1>
      </div>
      
      <!-- Subtitle with input -->
      <div class="mb-12">
        <p class="text-xl md:text-2xl mb-6 opacity-90">
          月下共頻的畫面來自
        </p>
        
        <!-- Name Input -->
        <div class="mb-8">
          <div class="w-64 mx-auto">
            <input 
              v-model="userName"
              type="text" 
              placeholder="車主名稱"
              class="w-full px-2 py-3 text-lg text-center bg-transparent border-none text-white placeholder-gray-400 focus:outline-none focus:placeholder-gray-300 transition-all"
              @keyup.enter="startCamera"
            />
            <div class="h-px bg-gradient-to-r from-transparent via-white/50 to-transparent mt-2"></div>
          </div>
        </div>
      </div>
      
      <!-- Description -->
      <div class="mb-12">
        <p class="text-lg md:text-xl mb-2 opacity-90">
          為你的探月護照
        </p>
        <p class="text-lg md:text-xl opacity-90">
          拍一張追月時的旅程照片
        </p>
      </div>
      
      <!-- Start Button -->
      <button 
        @click="startCamera"
        :disabled="!userName.trim()"
        class="px-16 py-3 text-base font-normal rounded-none flex items-center justify-center gap-2 min-w-[280px]"
        style="background-color: #ffffff !important; color: #000000 !important; border: none !important; opacity: 1 !important;"
        :class="{ 'cursor-not-allowed': !userName.trim() }"
      >
        <span>開啟相機</span>
        <span class="text-lg">&gt;</span>
      </button>
      
      <!-- Footer space -->
      <div class="mt-16"></div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'LandingPage',
  emits: ['start-camera'],
  setup(props, { emit }) {
    const userName = ref('')
    
    const startCamera = () => {
      if (userName.value.trim()) {
        emit('start-camera', userName.value.trim())
      }
    }
    
    return {
      userName,
      startCamera
    }
  }
}
</script>

