<template>
  <div id="app" class="min-h-screen bg-app-bg text-app-fg">
    <!-- Landing Page -->
    <LandingPage 
      v-if="!showCameraApp"
      @start-camera="handleStartCamera"
    />
    
    <!-- Camera App -->
    <div v-else>
      <!-- Header - 隱藏 -->
      <!-- <AppHeader 
        :status="detectionStatus"
        :mobile-menu-open="mobileMenuOpen"
        @toggle-menu="toggleMobileMenu"
      /> -->

      <!-- Main Layout -->
      <main class="grid grid-cols-1 h-screen">
      <!-- Control Panel - 隱藏 -->
      <!-- <ControlPanel 
        :class="{ 
          'translate-x-0': mobileMenuOpen, 
          '-translate-x-full': !mobileMenuOpen 
        }"
        class="fixed lg:static inset-y-0 left-0 w-[min(88vw,340px)] max-w-[94vw] lg:w-auto lg:max-w-none lg:translate-x-0 z-50 lg:z-auto transition-transform duration-300 bg-app-bg border-r border-app-border overflow-auto"
        :camera-running="cameraRunning"
        :segmentation-enabled="segmentationEnabled"
        :show-mask="showMask"
        :background-type="backgroundType"
        :effect-intensity="effectIntensity"
        :edge-feather="edgeFeather"
        :temporal-smooth="temporalSmooth"
        :threshold="threshold"
        :face-count="faceCount"
        :faces-in-area="facesInArea"
        @start-camera="startCamera"
        @stop-camera="stopCamera"
        @switch-camera="switchCamera"
        @update-segmentation="updateSegmentation"
        @update-show-mask="updateShowMask"
        @update-background-type="updateBackgroundType"
        @update-effect-intensity="updateEffectIntensity"
        @update-edge-feather="updateEdgeFeather"
        @update-temporal-smooth="updateTemporalSmooth"
        @update-threshold="updateThreshold"
        @test-mov-background="testMovBackground"
      /> -->

      <!-- Camera Stage -->
      <CameraStage 
        ref="cameraStageRef"
        class="relative bg-gray-900 overflow-hidden w-full h-full"
        :background-type="backgroundType"
        :is-i-o-s-device="isIOSDevice"
        :show-controls="!showPhotoPreview && !showProgressModal"
        @capture-photo="capturePhoto"
        @share-video="shareVideo"
        @download-video="downloadVideo"
      />
      
      <!-- 月亮影片已移到 CameraStage 內部 -->
    </main>

    <!-- Mobile Scrim -->
    <div 
      v-show="mobileMenuOpen" 
      class="fixed inset-0 bg-black bg-opacity-40 backdrop-blur-sm z-40 lg:hidden"
      @click="toggleMobileMenu"
    />

      <!-- Status Bar -->
      <StatusBar :status="statusText" />

      <!-- Modal Components -->
      <LoadingModal 
        v-if="showLoadingModal" 
        :message="loadingMessage" 
      />
      
      <ProgressModal 
        v-if="showProgressModal" 
        :progress="recordingProgress"
        :status="progressStatus"
        :show-controls="showProgressControls"
        @close="closeProgressModal"
        @share="shareFromModal"
        @download="downloadFromModal"
      />

      <!-- Photo Preview Modal -->
      <PhotoPreview
        v-if="showPhotoPreview"
        :original-image-data="previewImageData"
        :available-backgrounds="availableBackgrounds"
        :selected-background="selectedBackground"
        :user-name="userName"
        @close="hidePreview"
        @background-change="selectBackground"
      />

      <!-- Hidden DOM elements needed by segmentationCore.js -->
      <div class="hidden">
        <video ref="videoEl" class="input_video" playsinline />
        
        <video
          ref="bgVideoEl"
          class="bg_video"
          playsinline
          muted
          loop
          preload="auto"
          src="https://interactive-examples.mdn.mozilla.net/media/cc0-videos/flower.mp4"
        />

        <video
          ref="bgMoonVideoEl"
          class="bg_video"
          muted
          loop
          preload="auto"
          playsinline
          style="display: none;"
        >
        </video>
        
        <!-- iOS 專用透明 MOV 月亮影片 -->
        <video
          ref="iosMoonVideoEl"
          class="bg_video"
          muted
          loop
          preload="auto"
          playsinline
          webkit-playsinline
          style="display: none; pointer-events: none; background: transparent;"
        >
        </video>

      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount, nextTick, watch } from 'vue'
import LandingPage from './components/LandingPage.vue'
import AppHeader from './components/AppHeader.vue'
import ControlPanel from './components/ControlPanel.vue'
import CameraStage from './components/CameraStage.vue'
import StatusBar from './components/StatusBar.vue'
import LoadingModal from './components/LoadingModal.vue'
import ProgressModal from './components/ProgressModal.vue'
import PhotoPreview from './components/PhotoPreview.vue'
import { useMediaPipe } from './composables/useMediaPipe'
import { useUI } from './composables/useUI'

export default {
  name: 'App',
  components: {
    LandingPage,
    AppHeader,
    ControlPanel,
    CameraStage,
    StatusBar,
    LoadingModal,
    ProgressModal,
    PhotoPreview
  },
  setup() {
    // Page state
    const showCameraApp = ref(false)
    const userName = ref('')
    
    // Template refs
    const videoEl = ref(null)
    const bgVideoEl = ref(null)
    const bgMoonVideoEl = ref(null)
    const iosMoonVideoEl = ref(null)
    const fullscreenMoonEl = ref(null)
    const cameraStageRef = ref(null)
    
    // Base path for assets
    const basePath = import.meta.env.BASE_URL || '/'
    
    // iOS 設備檢測
    const detectIOSDevice = () => {
      const userAgent = navigator.userAgent.toLowerCase()
      const isIOS = /iphone|ipad|ipod/.test(userAgent)
      const isLineApp = /line/.test(userAgent)
      const isChrome = /crios/.test(userAgent)
      const isSafari = /safari/.test(userAgent) && !/chrome/.test(userAgent)
      
      // 🧪 測試模式：在 URL 加上 ?test-mov=1 可以在任何設備上測試 MOV
      const urlParams = new URLSearchParams(window.location.search)
      const isTestMode = urlParams.get('test-mov') === '1'
      
      console.log('🔍 設備檢測:', {
        userAgent,
        isIOS,
        isLineApp,
        isChrome,
        isSafari,
        isTestMode,
        shouldUseMOV: (isIOS && (isLineApp || isChrome || isSafari)) || isTestMode
      })
      
      // 正常邏輯 || 測試模式
      return (isIOS && (isLineApp || isChrome || isSafari)) || isTestMode
    }
    
    const isIOSDevice = ref(detectIOSDevice())
    
    // Handle start camera from landing page
    const handleStartCamera = async (name) => {
      userName.value = name
      showCameraApp.value = true
      
      // 等待下一個 tick 確保 DOM 已更新
      await nextTick()
      
      // 自動啟動相機
      try {
        await startCamera()
        // 設定為月亮背景但不啟用分割（保持原始相機畫面+月亮動效）
        updateBackgroundType('custom_moon_bg')
        updateSegmentation(false) // 拍照前不啟用分割
        console.log('✅ 自動啟動相機成功 - 預覽模式')
      } catch (error) {
        console.error('❌ 自動啟動失敗:', error)
      }
    }

    // MOV video event handlers for debugging
    const handleMovError = (event) => {
      console.error('🎬 MOV 載入錯誤:', event.target.error)
      console.log('🎬 嘗試載入的路徑:', event.target.src)
    }
    
    const handleMovLoadStart = (event) => {
      console.log('🎬 MOV 開始載入:', event.target.src)
    }
    
    const handleMovCanPlay = (event) => {
      console.log('🎬 MOV 可以播放:', event.target.src)
    }

    // UI state
    const {
      mobileMenuOpen,
      detectionStatus,
      statusText,
      showLoadingModal,
      loadingMessage,
      showProgressModal,
      recordingProgress,
      progressStatus,
      showProgressControls,
      showPhotoPreview,
      previewImageData,
      availableBackgrounds,
      selectedBackground,
      toggleMobileMenu,
      closeProgressModal,
      shareFromModal,
      downloadFromModal,
      showPreview,
      hidePreview,
      selectBackground,
      createPhotoFlashEffect,
      initKeyboardShortcuts
    } = useUI()

    // MediaPipe functionality
    const mediaPipeComposable = useMediaPipe({
      detectionStatus,
      statusText,
      showProgressModal,
      recordingProgress,
      progressStatus,
      showProgressControls,
      showPreview
    })
    
    // 解構 MediaPipe 狀態和方法
    const {
      cameraRunning,
      segmentationEnabled,
      showMask,
      backgroundType,
      effectIntensity,
      edgeFeather,
      temporalSmooth,
      threshold,
      faceCount,
      facesInArea,
      startCamera,
      stopCamera,
      switchCamera,
      updateSegmentation,
      updateShowMask,
      updateBackgroundType,
      updateEffectIntensity,
      updateEdgeFeather,
      updateTemporalSmooth,
      updateThreshold,
      capturePhoto,
      toggleMask,
      shareVideo,
      downloadVideo,
      initMediaPipe,
      handleResize,
      testMovBackground
    } = mediaPipeComposable

    // Initialize camera app when needed
    const initializeCameraApp = async () => {
      await nextTick()
      
      // 設定影片路徑
      if (bgMoonVideoEl.value) {
        const base = import.meta.env.BASE_URL || '/'
        bgMoonVideoEl.value.src = base + 'moon.mp4'
      }
      
      // iOS 設備設定透明 MOV 路徑
      if (iosMoonVideoEl.value && isIOSDevice.value) {
        const base = import.meta.env.BASE_URL || '/'
        iosMoonVideoEl.value.src = base + 'moon_alpha_ios.mov'
        console.log('🍎 iOS設備檢測到，載入透明MOV:', base + 'moon_alpha_ios.mov')
      }
      
      // 初始化快捷鍵
      initKeyboardShortcuts({
        startCamera,
        stopCamera,
        switchCamera,
        capturePhoto,
        cameraRunning
      })

      // 等待 CameraStage 組件完全掛載
      await nextTick()
      
      // 檢查 CameraStage 是否正確掛載
      if (!cameraStageRef.value) {
        console.warn('⚠️ CameraStage 引用未找到，延遲初始化')
        setTimeout(initializeCameraApp, 200)
        return
      }

      // 初始化 MediaPipe
      const elements = {
        videoEl: videoEl.value,
        bgCanvas: cameraStageRef.value?.bgCanvas,
        maskCanvas: cameraStageRef.value?.maskCanvas,
        outCanvas: cameraStageRef.value?.outputCanvas,
        bgVideoEl: bgVideoEl.value,
        bgMoonVideoEl: bgMoonVideoEl.value,
        iosMoonVideoEl: iosMoonVideoEl.value, // 使用隱藏的 DOM 元素，可隨時播放
        fullscreenMoonEl: cameraStageRef.value?.moonVideo, // CameraStage 內的月亮影片
        isIOSDevice: isIOSDevice.value
      }
      
      console.log('🔧 Elements 檢查:', {
        hasVideoEl: !!elements.videoEl,
        hasBgCanvas: !!elements.bgCanvas,
        hasMaskCanvas: !!elements.maskCanvas,
        hasOutCanvas: !!elements.outCanvas,
        isIOSDevice: elements.isIOSDevice
      })

      try {
        await initMediaPipe(elements)
      } catch (error) {
        console.error('❌ MediaPipe 初始化失敗:', error)
        // 在 iOS 設備上，即使 MediaPipe 失敗也可以繼續使用相機
        if (isIOSDevice.value) {
          console.log('🍎 iOS 設備：即使 MediaPipe 失敗也將繼續')
        }
      }

      // 響應式布局監聽
      window.addEventListener('resize', handleResize)
      
      // 自動開啟相機
      console.log('🎥 自動開啟相機...')
      setTimeout(() => {
        startCamera()
      }, 500) // 給一點時間讓初始化完成
    }

    // Watch for camera app activation
    const initCameraAppOnShow = () => {
      if (showCameraApp.value) {
        setTimeout(initializeCameraApp, 100) // 小延遲確保DOM渲染完成
      }
    }

    // Lifecycle
    onMounted(() => {
      // 監聽 showCameraApp 的變化
      watch(showCameraApp, initCameraAppOnShow)
    })

    onBeforeUnmount(() => {
      window.removeEventListener('resize', handleResize)
    })

    return {
      // Page state
      showCameraApp,
      userName,
      handleStartCamera,
      
      // Template refs
      videoEl,
      bgVideoEl,
      bgMoonVideoEl,
      iosMoonVideoEl,
      fullscreenMoonEl,
      cameraStageRef,
      
      // UI state
      mobileMenuOpen,
      detectionStatus,
      statusText,
      showLoadingModal,
      loadingMessage,
      showProgressModal,
      recordingProgress,
      progressStatus,
      showProgressControls,
      showPhotoPreview,
      previewImageData,
      availableBackgrounds,
      selectedBackground,
      toggleMobileMenu,
      closeProgressModal,
      shareFromModal,
      downloadFromModal,
      showPreview,
      hidePreview,
      selectBackground,
      createPhotoFlashEffect,

      // MediaPipe state
      cameraRunning,
      segmentationEnabled,
      showMask,
      backgroundType,
      effectIntensity,
      edgeFeather,
      temporalSmooth,
      threshold,
      faceCount,
      facesInArea,

      // Base path
      basePath,
      
      // Device detection
      isIOSDevice,
      
      // Methods
      startCamera,
      stopCamera,
      switchCamera,
      updateSegmentation,
      updateShowMask,
      updateBackgroundType,
      updateEffectIntensity,
      updateEdgeFeather,
      updateTemporalSmooth,
      updateThreshold,
      capturePhoto,
      toggleMask,
      shareVideo,
      downloadVideo,
      testMovBackground,
      
      // MOV handlers
      handleMovError,
      handleMovLoadStart,
      handleMovCanPlay
    }
  }
}
</script>